import AdminLayout from "@/layouts/Admin/index.vue";
import BotosRowPlaceHolder from "@/components/PlaceHolders/BotosRowPlaceHolder/index.vue";
import { Boto } from "@/interfaces/Boto";
import { defineComponent } from "vue";
import routes from "@/routes";
import Toggle from "@vueform/toggle";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import BotoModal from "@/components/BotoModal/index.vue";

const items: Boto[] = [];

export default defineComponent({
  name: "Botos",
  components: {
    AdminLayout,
    BotosRowPlaceHolder,
    Toggle,
    BotoModal,
  },
  setup() {
    const store = useStore();
    const route = useRoute();
    let bot = null;
    if (route.params.botId) {
      bot = {
        id: route.params.botId,
        name: route.params.botName,
        frequency: route.params.botFrequency,
      };
    }
    return {
      store,
      bot,
    };
  },
  data() {
    return {
      items,
      isLoadingData: false,
      errorText: "",
      error: false,
      activationModal: false,
    };
  },
  async mounted() {
    // Get Blocks
    try {
      if (this.bot) {
        this.activationModal = true;
      }
      this.isLoadingData = true;
      const botos = await this.getBotos();
      this.items = botos;
    } catch (e) {
      this.error = true;
      this.errorText = e;
    } finally {
      this.isLoadingData = false;
    }
  },
  methods: {
    getFrequencyWithUnit(bot: any) {
      const unit = bot.json.ux && bot.json.ux.frequencyUnit ? bot.json.ux.frequencyUnit : "minutes";
      const frequency = unit === "days" ? bot.frequency / 24 / 60 : bot.frequency;
      console.log("json.ux", frequency, unit);
      return `${frequency} ${unit}.`;
    },
    closeActivatedBotMessage() {
      this.activationModal = false;
    },
    async getBotos() {
      const response = await fetch("https://botoapp.io/api/my/bots/", {
        cache: "no-store",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Token ${this.store.getters.localStorageAuthToken}`,
        },
      });
      const data = await response.json();
      return data;
    },
    async switchBoto(id: number) {
      try {
        const status = this.items[id].status;
        const name = this.items[id].name;
        const headers = new Headers();
        const formdata = new FormData();
        const url = `https://botoapp.io/api/bot/${this.items[id].id}/`;
        formdata.append("name", name);
        formdata.append("status", status);
        formdata.append("frequency", this.items[id].frequency.toString());
        const requestOptions = {
          method: "PUT",
          headers,
          body: formdata,
        };
        headers.append("Authorization", `Token ${this.store.getters.localStorageAuthToken}`);
        const response = await fetch(url, requestOptions);
        if (!response.ok) {
          throw new Error(`An error has occured: ${response.status}`);
        }
      } catch (e) {
        // TODO
        console.error(e);
      }
    },
    gotoBoto(id: number) {
      routes.push({ name: "home.editor", params: { id } });
    },
  },
});
